/* ==================== common styles ======================= */
html {
  font-size: 14px;
  /*used for rem-units*/
  overflow-x: hidden;
}
body {
  min-width: 320px;
  padding-top: 177px;
  overflow-x: hidden;
  color: #000;
  font-family: $pr;
  font-size: 18px;
  transition: all 0s .4s;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
.hidden {
  display: none;
}
.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0,0,0, .5);
  z-index: 7;
}
.flex {
  display: flex;
}
a {
  text-decoration: none;
}
img,
a,
input,
input:focus {
  outline: none;
  border: none;
}
::-webkit-input-placeholder {
  color: #888;
}
::-moz-placeholder {
  color: #888;
}
:-ms-input-placeholder {
  color: #888;
}
::placeholder {
  color: #888;
}
section {
  position: relative;
}
.wrapper {
  position: relative;
  width: 96%;
  max-width: 1110px;
  margin: 0 auto;
}
.wrapper-w {
  position: relative;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
}
h2 {
  font-family: $osxb;
  font-size: 36px;
  line-height: 1.5;
  margin-bottom: 5px;
  color: $color2;
}
h3 {
  font-family: $osxb;
  font-size: 30px;
  line-height: 1.5;
  margin-bottom: 5px;
  color: $color2;
}
.button {
  display: inline-block;
  position: relative;
  top: 0;
  width: 100%;
  max-width: 255px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background: $green;
  color: $light;
  font-family: $osxb;
  border-radius: 28px;
  box-shadow: none;
  transition: all .4s;
  cursor: pointer;
  &:hover {
    top: -2px;
    box-shadow: 0px 8px 15px rgba(0,0,0,.3);
  }
}
p {
  font-family: $pr;
  font-size: 18px;
  line-height: 1.67;
  color: $color3;
  margin-bottom: 26px;
}
/*------------------------------ header ------------------------------*/
.header {
  z-index: 3;
  box-shadow: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(255,255,255, .92);
  &.fixed {

    box-shadow: 0px 5px 5px rgba(80,80,80,.1);
    .header-top {
     padding: .8% 0 .3%;
    }
    .logo {
      width: 160px;
      margin-right: 40px;
    }
    .header-button {
      margin-bottom: 2px;
      height: 32px;
      line-height: 32px;
      font-size: 16px;
    }
    .header-bottom {
      padding-bottom: 4px;
    }
    .nav-links a {
      padding-top: 2px;
      font-size: 16px;
    }

  }
}
.header-top {
  justify-content: space-between;
  align-items: flex-start;
  align-content: flex-start;
  padding: 2.95% 0 1.35%;
  transition: all .4s;
}
.top-contacts {
  margin: 14px 0 20px;
}
.phone {
  font-family: $osxb;
  font-size: 18px;
  color: $color1;
  margin: 0 47px 10px 0;
}
.mail {
  font-family: $osr;
  font-size: 18px;
  color: $color1;
}
.logo {
  margin-right: 44px;
  transition: all .4s;
  width: 224px;
  text-align: center;
  img {
    max-width: 100%;

  }
}
.header-button {
  font-size: 16px;
  height: 46px;
  line-height: 46px;
  margin: 0 0 20px;
  transition: all .4s;
}
.header-bottom {
  padding-bottom: 18px;
  transition: all .4s;
}
.nav-links {
  justify-content: center;
  li {
  padding: 0 27px;
  }
  a {
    display: inline-block;
    font-family: $osr;
    padding: 10px 0 5px;
    font-size: 18px;
    color: $color1;
    border-bottom: 1px solid transparent;
    transition: all .4s;
    &:hover {
      border-bottom-color: $color1;
    }
  }
}
/*------------------------------ main slider section ------------------------------*/
.main-slider-section {
  padding: 57px 0 3%;
  background-image: url(../img/mainSliderBg.jpg);
  background-repeat: repeat-x;
  background-size: auto 100%;
  height: 900px;
  max-height: 100vh;
}

.main-slider-item {
  position: relative;
  white-space: nowrap;
  overflow: hidden;
  &:before {
    content: "";
    display: inline-block;
    width: 40%;
    height: 1px;
    vertical-align: top;
  }
  
}
.main-slider-text {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 43.6%;
  transform: translateY(-50%);
  width: 40%;
  transition: all 1.2s;
}
.slick-active {
  .main-slider-heading {
    left: 0;
  }
  .collection-link {
    left: 0;
  }
}
.main-slider-heading {
  position: relative;
  left: 150vw;
  font-size: 90px;
  color: $color2;
  font-family: $osxb;
  margin-bottom: 44px;
  transition: all 1.2s;
}
.collection-link {
  display: inline-block;
  position: relative;
  left: -150vw;
  padding-left: 38px;
  font-family: $ossb;
  font-size: 18px;
  color: $color2;
  transition: all 1.2s;
  z-index: 2;
  &:hover {
    text-decoration: underline;
  }
  &:before,
  &:after {
    content: "";
    display: inline-block;
    position: absolute;

    transform: translateY(-50%);
  }
  &:before {
    width: 56px;
    height: 56px;
    background: $light;
    border-radius: 50%;
    top: 50%;
    left: 16px;
    box-shadow: -1px -1px 15px rgba(0,0,0,.5);
    z-index: -1;
  }
  &:after {
    @include sprite($s-arrow-r-lg);
    top: 68%;
    right: -35px;
  }
}
.main-slider-img {
  display: inline-block;
  position: relative;
  // position: absolute;
  // top: 50%;
  // right: 0;
  // transform: translateY(-50%);
  padding-top: 40px;
  width: 60%;
  // height: 900px;
  // max-height: 90vh;
}
.main-slider-item-bg {
  display: block;
  position: absolute;
  top: -200%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  max-width: 100%;
  max-height: 65vh;
  z-index: 0;
  transition: all 1.2s;
}
.slick-active {
  .main-slider-item-bg {
    top: 52%;
  }
}
.main-slider-item-img {
  // position: absolute;
  // top: 45%;
  // left: 50%;
  // transform: translate(-50%, -50%);
  display: block;
  margin: 0 auto;
  position: relative;
  max-width: 90%;
  max-height: 80vh;
  z-index: 1;
  top: 100vh;
  transition: all 1.2s;
}
.slick-active {
  .main-slider-item-img {
    top: 0;
  }
}
.js-main-slider .slick-dots {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  li {
    display: block;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    background: #cecece;
    margin-bottom: 10px;
    transition: all 1.2s;
    cursor: pointer;
    &.slick-active {
      height: 50px;
      background: $green;
    }
    button {
      display: none;
    }
  }
}
/*------------------------------ category section ------------------------------*/
.categories-section {
  margin-bottom: 5.9%;
}

.categories-container {
  -webkit-flex-wrap: wrap;
  -moz-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  -o-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-bottom: 3.5%;
}
.category-item {
  display: inline-block;
  position: relative;
  width: 25%;
  box-shadow: 0;
  overflow: hidden;
  transition: all .4s;
  &:hover {
    transform: scale(1.1);
    box-shadow: 0px 0px 15px rgba(0,0,0,.5);
    z-index: 1;
  }
  img {
    display: block;
    width: 100%;
  }
}
.category-text {
  position: absolute;
  left: 12%;
  bottom: 11%;
  line-height: 1.2;
  font-size: 30px;
  font-family: $osxb;
  color: $light;
  max-width: 66.7%;
  &.dark-font {
    color: $color2;
  }
}
/*------------------------------ small form section ------------------------------*/
.smallForm-section {
  .wrapper {
    justify-content: space-between;
    -ms-align-items: center;
    align-items: center;
  }
}
.heading {
  font-family: $osxb;
  font-size: 36px;
  color: #000;
  line-height: 1.3;
  margin-bottom: -2px;
}
.heading-after {
  font-family: $ossb;
  font-size: 18px;
  color: $color2;
  margin-bottom: 10px;
}
.small-form {
  input[type="email"] {
    height: 40px;
    line-height: 40px;
    border: none;
    border-bottom: 2px solid #000000;
    transition: all .4s;
    margin-bottom: 10px;
    margin-right: 68px;
    width: 284px;
    &:focus {
      border-bottom-color: $green;
    }
  }
  input[type="submit"] {
    width: 183px;
  }
}
.smallForm-heading {
  display: inline-block;
  width: 49%;
  
}
.smallForm-container {
  display: inline-block;
  white-space: nowrap;
  width: 50%;
  text-align: right;
  ::-webkit-input-placeholder {
    font-family: $ossb;
    font-size: 14px;
    color: #a3a3a3;
    letter-spacing: 1px;
  }
  ::-moz-placeholder {
    font-family: $ossb;
    font-size: 14px;
    color: #a3a3a3;
    letter-spacing: 1px;
  }
  :-ms-input-placeholder {
    font-family: $ossb;
    font-size: 14px;
    color: #a3a3a3;
    letter-spacing: 1px;
  }
  ::placeholder {
    font-family: $ossb;
    font-size: 14px;
    color: #a3a3a3;
    letter-spacing: 1px;
  }
  
}
/*------------------------------ works slider ------------------------------*/
.works-section {
  position: relative;
}
.works-slider-container {
  margin: 20px 0;
}
.works-slider-item {
  position: relative;
  height: 590px;

  .wrapper {
    height: 100%;
    background: rgba(255,255,255, .7);
  }
  .heading {
    line-height: 1.1;
    margin-bottom: 17px;
  }
  .heading-after {
    margin-bottom: 25px;
  }
  .button {
    width: 235px;
  }
}
.works-slide-textPart {
  max-width: 445px;

  height: 100%;
  padding-top: 7.8%;
}
.works-slide-imgPart {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  //max-width: 880px;
  width: 50%;

  img {
    //height: 100%;
    position: relative;
    width: 100%;
    transition: all .4s;
  }
  .heading {
    position: absolute;
    bottom: 10%;
    font-size: 34px;
    padding-left: 11.9%;
  }
}
.button.slide-button {
  height: 60px;
  line-height: 60px;
  font-family: $osb;
  font-size: 16px;
}
.slider-custom-arrows {
  position: absolute; 

  bottom: 0;
  span {
    display: inline-block;  
    width: 40px;  
    height: 40px; 
    margin: 5px;
    box-shadow: 0 0 25px rgba(0,0,0,.4);
    border-radius:  50%;
    background-position:  50% 50%;
    background-repeat: no-repeat;
    cursor: pointer;
    opacity: .5;
    transition: all .4s;
    &:hover,
    &:active {
      opacity: 1;
    }
  }
}
.slider-custom-prev {
  background-image: url(../img/sprite/arrow-l-sm.png);
  
}
.slider-custom-next {
  background-image: url(../img/sprite/arrow-r-sm.png);
}
.js-works-slider,
.js-manufactory-slider {
  .slick-dots {
    position: absolute;
    bottom: 4%;
    right: 43%;
    li {
      display: inline-block;
      button {
        outline: none;
        font-size: 0;
        margin: 0 6px;
        border: 1px solid #cecece;
        background: #cecece;
        cursor: pointer;
      }
    }
  }
}
.works-slider-container {
  .slider-custom-arrows {
    right: 21%;
  }
}
.works-slider-container,
.manufactory-slider-container {
  .slick-dots {
    li {
      display: inline-block;
      button {
        font-size: 0;
        padding: 0 4px;
        border-radius: 50%;
        width: 10px !important;
        max-width: 10px;
        height: 10px;
      }
      &.slick-active {
        button {
          background: transparent;
        }
      }
    }
  }
}

.leaves {
  position: absolute;
  top: 54%;
  z-index: -1;
  
}

.leaves-l {
  left: 0;
  transform: scale(-1, 1);
  transition: all 3s cubic-bezier(.53,1.9,.35,.89);
}

.leaves-l {
  right: 0;
}

/*------------------------------ manufactory section  ------------------------------*/
.manufactory-section {
  position: relative;
  .wrapper {
    background: rgba(255,255,255,.7);
  }
  .heading {
    margin-bottom: 5px;
    max-width: 340px;
  }
  .heading-after {
    margin-bottom: 26px;
  }
  .slider-custom-arrows {
    left: 40.5%;
    bottom: 15%;
    span {
      background-color: #fff;
    }
  }
}
.manuf-slider-item {
  position: relative;
}
.manuf-slide-imgPart {
  position: absolute;
  left: 0;
  top: 0;
  width: 50%;
  .heading {
    position: absolute;
    right: 20%;
    bottom: 20%;
    color: #fff;
    font-size: 34px;
  }
  img {
    max-width: 100%;
  }
  .video-prev-container {
    max-width: 100%;
  }
}
.manuf-slide-textPart {
  padding-left: 59.5%;

  min-height: 710px;
  height: 100%;
  padding-top: 12.8%;
}
.video-prev-container {
  outline: none;
  display: block;
  position: relative;
  z-index: 2;
  &:after {
    content: "";
    display: block;
    width: 107px;
    height: 75px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-image: url(../img/youtube-logo.png);
  }
  .button.slide-button {
    max-width: 280px;
  }
}
.js-manufactory-slider .slick-dots {
    left: 20.3%;
    right: auto;
    bottom: 18.1%;
}

.leaves-r {
  right: 0;
  transition: all 4s cubic-bezier(.53,1.9,.35,.89);
}
/*------------------------------ about section ------------------------------*/
.about-section {
  margin-bottom: 4.4%;
  .wrapper {
    white-space: nowrap;
  }
}
.features-part,
.about-text-part {

  display: inline-block;
  width: 49.9%;
  white-space: normal;
  padding-top: 14%;
  h3 {
    line-height: 1.2;
    margin-bottom: 25px;
    max-width: 55%;
  }
}
.about-text-part {

  position: relative;
  vertical-align: top;
  padding: 14% .8% 6% 9.4%;
  &:after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 200vw;
    background: #f3f3f3;
    z-index: -1;
  }
  h2 {
    font-size: 37px;
    margin-bottom: 5%;
  }
  h4 {
    font-family: $ossb;
    font-size: 18px;
    color: #0a0a0a;
    margin-bottom: 6%;
    max-width: 285px;
    line-height: 1.2;
  }
  p {
    margin-bottom: 5%;
  }
  .button {
    font-size: 16px;
    width: 255px;
    height: 55px;
    line-height: 55px;
  }
}
.feature-text {
  font-size: 16px;
  color: #000;
  font-family: $osr;
}
.feature-img  {
  margin-bottom: 17px;
}
.feature-proection {
  margin-bottom: 34px;
}
.proection-img {
  display: block;
  @include sprite($s-modeling);
}
.time-img {
  display: block;
  @include sprite($s-time);
}
.eco-img {
  display: block;
  @include sprite($s-eco);
}
.feature-manufacturing {
  >span {
    display: inline-block;
    margin-right: 14%;
    margin-bottom: 15px;
  }
}
/*------------------------------ testimonials section ------------------------------*/
.tesimonials-section {
  padding: 5% 0 6%;
  background: url(../img/wood.jpg);
}
.testimonials-heading {
  font-family: $osxb;
  font-size: 36px;
  color: $light;
  margin-bottom: 8px;
}
.testimonials-subheading {
  font-family: $ossb;
  color: $light;
  font-size: 18px;
  line-height: 1.2;
  max-width: 285px;
  margin-bottom: 42px;
}
.testimonial-link {
  display: block;
  padding: 10px 15px;
  img {
    max-width: 100%;
  }
}
.testimonials-slider-container {
  position: relative;
  margin: 0 -15px;
  .wrapper {
    overflow: hidden;
  }
  
  .slick-dots {
    text-align: center;
    padding-top: 20px;
    li {
      display: inline-block;
      button {
        background: none;
        border: none;
        outline: none;
        font-size: 18px;
        color: $light;
        cursor: pointer;
        margin: 10px 5px;
        padding: 0;
        border-bottom: 1px solid transparent;
        transition: all .4s;
      }
      
    }
    .slick-active {
      button {
        font-family: $osb;
        border-bottom-color: $light;
      }
    }
  }
}
.testimonial-arrow {
  position: absolute;
  bottom: 4px;
  display: inline-block;
  opacity: .7;
  transition: all .4s;
  cursor: pointer;
  &:hover,
  &:active {
    opacity: 1;
  }
}
.testimonial-prev {
  left: 15px;
  @include sprite($s-arrow-l-light);
}
.testimonial-next {
  right: 15px;
  @include sprite($s-arrow-r-light);
}
/*------------------------------ clients section ------------------------------*/
.clients-section {
  padding: 2.8% 0 2.4%;
}
.clients-slider-container {
  position: relative;
  img {
    display: block;
    padding: 10px 50px;
  }
}
.clients-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  opacity: .7;
  transition: all .4s;
  &:hover,
  &:active {
    opacity: 1;
  }
}
.clients-prev {
  left: 0;
  @include sprite($s-arr-l-md);
}
.clients-next {
  right: 0;
  @include sprite($s-arr-r-md);
}

/*------------------------------ contacts section ------------------------------*/
.contacts-section {
  position: relative;
  .wrapper {
    position: relative;
    // height: 80vh;
    // min-height: 240px;
    // max-height: 470px;
  }
}

.map {
  height: 80vh;
  min-height: 240px;
  max-height: 470px;
  background: #888;
}

.main-form-container {
  position: absolute;
  left: 0;
  top: -510px;
  background: #292929;
  padding: 5% 6% 60px;

  width: 445px;
  max-width: 90%;
  box-shadow: 0px 0px 20px rgba(0,0,0,.4);
}

.form-heading {
  font-family: $osxb;
  font-size: 24px;
  color: $light;
  padding-bottom: 7%;
}

.main-form {
  position: relative;
  ::-webkit-input-placeholder {
    color: #fff;
    font-size: 18px;
    line-height: 27px;
  }
  ::-moz-placeholder {
    color: #fff;
    font-size: 18px;
    line-height: 27px;
  }
  :-ms-input-placeholder {
    color: #fff;
    font-size: 18px;
    line-height: 27px;
  }
  ::placeholder {
    color: #fff;
    font-size: 18px;
    line-height: 27px;
  }
  input[type="text"],
  input[type="tel"],
  input[type="email"] {
    font-family: $osr;
    height: 58px;
    line-height: 58px;
    background: transparent;
    width: 100%;
    font-size: 18px;
    margin-bottom: 8.3%;
    border-bottom: 2px solid #e8e8e8;
    transition: all .4s;
    color: $light;
    &:focus {
      border-color: $green;
    }
  }
  textarea {
    height: 110px;
    resize: none;
    background: transparent;
    width: 100%;
    font-size: 18px;
    border: none;
    outline: none;
    border-bottom: 2px solid #e8e8e8;
    transition: all .4s;
    color: $light;
    font-family: $osr;
    overflow: hidden;
    &:focus {
      border-color: $green;
    }
  }
  input[type="submit"] {
    position: absolute;
    right: 0;
    bottom: -100px;
    display: inline-block;
    width: 82px;
    height: 83px;
    border-radius: 50%;
    background: transparent;
    background-image: url(../img/send-btn.png);
    box-shadow: 0px 0px 20px rgba(0,0,0,.4);
    cursor: pointer;
    transition: all .4s;
  }
}


/*------------------------------ footer ------------------------------*/
.footer {
  padding: 4.9% 0 4.5%;

}

.footer-nav {
  li {
    padding: 0 23px;
  }
  li:last-child {
    padding-right: 0;
  }
}

.footer-top {
  justify-content: space-between;
  padding-bottom: 5.6%;
  border-bottom: 1px solid #7f7f7f;

}

.footer-bottom {
  justify-content: space-between;
  -ms-align-items: center;
  align-items: center;
}

.copy {
  margin: 10px 0 10px;
  font-family: $ossl;
  font-size: 14px;
  color: $color2;
}

.footer-logo {
  max-width: 190px;
}

/*------------------------------ modal form ------------------------------*/

.modal-container {
  display: none;
  position: fixed;
  max-height: 100vh;
  padding-bottom: 30px;
  top: 50%;
  left: 50%;
  max-width: 100%;
  transform: translate(-50%, -50%);
  overflow-y: auto;
  z-index: 8;
  .main-form-container {
    position: relative;
    top: 0;
  }
}

.modal-close {
  position: absolute;
  top: 15px;
  right: 15px;
  color: $green;
  font-size: 28px;
  cursor: pointer;
}